import { all, fork, takeLatest, call, put, delay } from 'redux-saga/effects';
import {
  SIGNUP_FAILURE,
  SIGNUP_SUCCESS,
  SIGNUP_REQUEST,
  SIGNIN_SUCCESS,
  SIGNIN_FAILURE,
  SIGNIN_REQUEST,
  SIGNUP_FINISH,
} from '../actions/userAction';
import { signUpApi, signInApi } from '../../api/userApi';

function* signIn(action) {
  const { memberId, password, isStayLogin } = action.data;
  try {
    const { data } = yield call(() => signInApi({ memberId, password }));
    yield put({
      // put dispatch 
      type: SIGNIN_SUCCESS,
      data: { ...data },
    });
    if (isStayLogin) {
      localStorage.setItem('USER', JSON.stringify(data));
      localStorage.setItem('TOKEN', data.jwt); //    
    }
  } catch (e) {
    // signupAPI 
    console.log(e.response.data.errorMessage);
    yield put({
      type: SIGNIN_FAILURE,
      error: e.response.data.errorMessage,
    });
  }
}

function* watchSignIn() {
  yield takeLatest(SIGNIN_REQUEST, signIn);
}

function* signUp(action) {
  try {
    const { data } = yield call(() => signUpApi(action.data));
    yield put({
      type: SIGNUP_SUCCESS,
    });
    yield put({
      //   
      type: SIGNIN_SUCCESS,
      data: { ...data },
    });
    localStorage.setItem('USER', JSON.stringify(data));
    localStorage.setItem('TOKEN', data.jwt); //    
    yield put({
      type: SIGNUP_FINISH,
    });
  } catch (e) {
    const errorArray = [];
    const {
      response: {
        data: { errors },
      },
    } = e;
    // signupAPI 
    if (errors.length > 0) {
      errors.forEach(object => object.errorMessage && errorArray.push(object.errorMessage));
    }
    console.log(errors);
    //   
    yield put({
      type: SIGNUP_FAILURE,
      error: errorArray.toString(),
    });
  }
}

function* watchSignUp() {
  yield takeLatest(SIGNUP_REQUEST, signUp);
}

export default function* userSaga() {
  yield all([fork(watchSignUp), fork(watchSignIn)]);
}
